@import url('https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
         margin: 0;
         padding: 0;
}

body {
         background-color: #505050;
         color:aliceblue;
         font-family: "PT Sans", sans-serif;
         font-weight: 400;
         font-style: normal;

}

.hhh {
         background-color: black;
         padding: 20px 40px; /*сверху/снизу 20рх, справа/слева 40рх*/

         font-weight: 700;
         font-style: normal;
}
main {
         margin: 20px 40px;
         width: 30%;
         float: left; /*позиционирование*/
}

aside {
         background-color: #E5E8EF;
         margin-top: 40px;
         margin-left: 5%;
         float: left;
}

main .user {
         padding: 20px;
         width: 400px;
         background-color: #4d7ab8;
         border-radius: 4px;
         margin-bottom: 20px;
         box-shadow: 2px 2px 2px #00000038;
         
}

main .usersEmpty {
         width: 100%;
         padding: 40px;
         background-color: #e9dede13;
         float: center;
}